<template>
	<view class="container">
		<u-dropdown :close-on-click-mask="true" ref="uDropdown" active-color="#03958C">
			<u-dropdown-item v-model="value1" title="分组" :height="420" :options="groups" @change="groupsTap"></u-dropdown-item>
			<u-dropdown-item v-model="value2" title="排序" :height="420" :options="select"  @change="selectTap"></u-dropdown-item>
			<u-dropdown-item title="筛选">
				<view class="slot-content">
					<view class="screenItem">
						<view style="padding-bottom: 140rpx;">
							<text class="title">客户状态</text>
							<view class="screenList">
								<view class="label_list" :class="{'active': selectStatus.indexOf(item.value)!=-1}" v-for="(item,index) in statusList"
								 :key="index" @click="addStatusTags(item.value)">
									{{ item.label }}
								</view>
							</view>
							<text class="title">意向级别</text>
							<view class="screenList">
								<view class="label_list" :class="{'active': selectInten.indexOf(item.value)!=-1}" v-for="(item,index) in intentionList"
								 :key="index" @click="addIntenTags(item.value)">
									{{ item.label }}
								</view>
							</view>
						</view>

						<view class="selectBtn">
							<view class="clean" @click="cleanTap">
								<image src="@/static/salePerson/clean.png" mode=""></image>
								<text>清除</text>
							</view>
							<view class="sure" @click="sure">确定</view>
						</view>
					</view>
				</view>
			</u-dropdown-item>
		</u-dropdown>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		props:['groups','intentionList','statusList'],
		data() {
			return {
				value1: '-1',
				value2: '',
				select: [{
						label: '最新跟进',
						value: 0,
					},
					{
						label: '最早跟进',
						value: 1,
					},
					{
						label: '最新创建',
						value: 2,
					},
					{
						label: '最新认购',
						value: 3,
					},
				],
				selectStatus: [], // 选中的客户状态
				selectInten: [], // 选中的意向级别
			}
		},
		onLoad(){
			this._getSelectList()
		},
		methods: {
			// 分组回调
			groupsTap(){
				this.$emit('groupsTap',this.value1)
			},
			// 排序回调
			selectTap(){
				this.$emit('selectTap',this.value2)
			},
			// 选择客户状态
			addStatusTags(index) {
				let that = this;
				if (that.selectStatus.indexOf(index) == -1) {
					that.selectStatus.push(index); //选中添加到数组里
				} else {
					that.selectStatus.splice(that.selectStatus.indexOf(index), 1); //取消
				}
			},
			// 选择意向级别
			addIntenTags(index) {
				let that = this;
				if (that.selectInten.indexOf(index) == -1) {
					that.selectInten.push(index); //选中添加到数组里
				} else {
					that.selectInten.splice(that.selectInten.indexOf(index), 1); //取消
				}
			},
			// 清除选中
			cleanTap() {
				this.selectStatus = []
				this.selectInten = []
			},
			// 确定筛选/回调
			sure() {
				this.$refs.uDropdown.close();
				this.$emit('sureTap',this.selectStatus,this.selectInten)
			},
		},
		components: {

		}
	}
</script>

<style lang="scss">
	.container{
		background-color: #f6f6f6;
	}
	.u-dropdown__menu {
		border-bottom: 1rpx solid #eee;
	}
	.screenItem {
		background-color: #fff;
		max-height: 500rpx;
		overflow-y: scroll;
		position: relative;

		.title {
			font-size: 30rpx;
			display: flex;
			padding: 20rpx;
		}

		.screenList {
			display: flex;
			flex-wrap: wrap;
			padding: 0 20rpx;

			.label_list {
				background-color: #f6f6f6;
				padding: 10rpx 20rpx;
				color: #999;
				font-size: 24rpx;
				margin: 10rpx;
				border-radius: 6rpx;
			}

			.active {
				background-color: #03958C;
				color: #fff;
			}
		}
			/* #ifdef H5 */
		.selectBtn{
			width: 375rpx;
		}
		/* #endif */
		.selectBtn {
			width: 750rpx;
			height: 120rpx;
			position: fixed;
			bottom: 0;
			border-top: 1rpx solid #eee;
			display: flex;
			justify-content: space-between;
			align-items: center;
			background-color: #fff;

			view {
				flex: 1;
				margin: 30rpx;
				font-size: 32rpx;
			}

			.clean {
				display: flex;
				flex-direction: column;
				align-items: center;

				image {
					width: 40rpx;
					height: 40rpx;
					padding-bottom: 8rpx;
				}

				text {
					font-size: 30rpx;
					color: #999;
				}
			}

			.sure {
				min-width: 520rpx;
				height: 80rpx;
				line-height: 80rpx;
				text-align: center;
				background-color: #03958C;
				text-align: center;
				color: #fff;
				border-radius: 10rpx;
			}
		}
	}
</style>
